import axiosInstance from "@/core/services/axios/axiosInstance";
import { API, getEndpoint } from "@/core/constant/apis";
import { UserCreationRequest, UserCreationResponse, UserRequest, UserUpdateRequest,UserStatusRequest } from "../types/user.type";

export const userApi = {
  userCreation: async (userData: UserCreationRequest): Promise<UserCreationResponse> => {
    const { data } = await axiosInstance.post<UserCreationResponse>(
      // getEndpoint("NUSERS.USER.CREATE"),
      API.NUSERS.USER.CREATE,
      userData 
    );
    return data;
  },
  userStatusUpdate: async (userData: UserStatusRequest): Promise<UserUpdateRequest> => {
    const { user_id, ...updateData } = userData; 

    const { data } = await axiosInstance.put<UserUpdateRequest>(
     API.NUSERS.USER.STATUS_UPDATE + `/${user_id}/`, 
      updateData 
    );

    return data;
},
userUpdate: async (user_id:number,userData: UserUpdateRequest): Promise<UserUpdateRequest> => {
  // const { hashed_key, ...updateData } = userData; 
  // const { user_id, ...updateData } = userData; 

  // const { data } = await axiosInstance.put<UserUpdateRequest>(
  //   // `${getEndpoint("NUSERS.USER.UPDATE")}/${hashed_key}`, 
  //   API.NUSERS.USER.UPDATE + `/${hashed_key}`,
  //   updateData 
  // );
  const { data } = await axiosInstance.put<UserUpdateRequest>(
    API.NUSERS.USER.UPDATE + `${user_id}/`, 
    userData 
   );

  return data;
},


getProducts: async () => {
  const { data } = await axiosInstance.get<UserCreationResponse>(
    // getEndpoint("NUSERS.USER.PRODUCTS")
    API.NUSERS.USER.PRODUCTS
  );
  return data; 
},

};
